import React from 'react';
import buttonup from '../assets/button-up.png';
import '../styles/card.css'
function Card ({username,followers,todayfollow,icon,nameColor}){ 
/*Clase calculada*/
const cardClass = `card ${nameColor}`;
return(
    <article className={cardClass}>
                <p className="card-title">
                    <img src={icon } width="20px" height="20px" alt=""/>
                    {username}
                </p>
                <p className="card-followers">
                    <span className="card-followers-number">{followers}</span>
                    <span className="card-followers-title">Followers</span>
                </p>
                <p className="card-today">
                    <img src={buttonup } width="10px" height="10px" alt=""/>
                    {todayfollow} Today
                </p>
     </article>
)

}

export default Card; 

